@media only screen and (max-width: 1140px) {
  .header-dashboard-wrapper .header .menu-wrapper .menubar ul li {
    .nav-link {
      padding: 0.5rem 0.4rem;
      font-size: 14px;
    }
  }
}

@media only screen and (max-width: 1025px) {
  .header-dashboard-wrapper .header .menu-wrapper .menubar ul li {
    margin: 0px 10px;
  }
  .dashboard-section .details-action-container .details-action-wrapper h2 {
    font-size: 39px;
    font-weight: 600;
    line-height: 58px;
  }
  .header-dashboard-wrapper .header .menu-wrapper .menubar ul li {
    .nav-link {
      padding: 0.5rem 0.4rem;
    }
    .dashboard-section .details-action-container .details-action-wrapper h2 {
      font-size: 44px;
    }
    .start-online-chat-section .img-chat-wrapper .chat-wrapper {
      width: 37%;
      left: 290px;
      top: -4px;
      font-size: 8px;
    }

    .footer .about-webpage-wrapper .app-store-wrapper {
      display: block;
    }
  }

  .header-dashboard-wrapper
    .header
    .menu-wrapper
    .login-wrapper
    .number-wrapper {
    font-size: 0.7rem;
  }

  .start-online-chat-section {
    display: block;
    .img-chat-wrapper,
    .consult-doctor-details-wrapper {
      width: 100%;
    }
  }

  .start-online-chat-section .consult-doctor-details-wrapper {
    margin-top: 29px;
  }

  .consulting-specialist-section .small-boxes-wrapper .boxes-sm-bx {
    width: calc((100% / 4));
    padding: 8px;
  }

  .our-recent-articles-section
    .articles-wrapper
    .article-box
    .article-texts-wrapper
    h4 {
    font-size: 16px;
  }

  .asked-question-section
    .faq-img-wrapper
    .img-faq-sec
    .round-div
    .img-wrapper {
    right: 10px;
  }

  .footer .about-webpage-wrapper,
  .footer .contact-wrapper,
  .footer .important-wrapper,
  .footer .get-wrapper,
  .footer .extra-stuff-wrapper {
    width: 50%;
  }
  .footer .contact-wrapper {
    text-align: right;
    ul {
      display: inline-block;
      text-align: left;
    }
  }

  .footer .important-wrapper,
  .footer .get-wrapper,
  .footer .extra-stuff-wrapper {
    width: 33%;
  }

  .dashboard-section .dr-image-wrapper .img-wrapper {
    overflow: hidden;
  }
}

@media only screen and (max-width: 920px) {
  .header {
    position: relative;

    .menu-icon-hide {
      display: block !important;
    }
  }
  .header-dashboard-wrapper .header .menu-wrapper .menubar {
    position: absolute;
    display: none;
    width: 186px;
    right: 0px;
    background: #fffdfc;
    top: 100%;
    z-index: 99;
    box-shadow: 0px 3px 4px #8d8d8d;
    ul {
      display: block;
      li {
        margin: 0px 12px;
        border-bottom: solid 1px #ddd;
      }
    }
    &.show-menu {
      display: block !important;
    }
  }
  .header .menu-wrapper .login-wrapper {
    display: flex;
    width: 47%;
  }
  .our-recent-articles-section
    .articles-wrapper
    .article-box
    .articles-records-wrapper {
    top: 22rem;
  }
  .our-recent-articles-section .articles-wrapper .article-box {
    width: 100%;
    margin: 0;
    margin-bottom: 24px;
    box-shadow: 0px 1px 4px #00000047;
    border-radius: 9px;
    margin-bottom: 5rem;
  }
  .our-recent-articles-section .articles-wrapper .article-box .article-img {
    height: 400px;
  }
  .our-recent-articles-section
    .articles-wrapper
    .article-box
    .doctor-details-wrapper {
    justify-content: start;
  }
  .our-recent-articles-section
    .articles-wrapper
    .article-box
    .article-texts-wrapper {
    text-align: center;
  }
  .our-recent-articles-section
    .articles-wrapper
    .article-box
    .doctor-details-wrapper
    .about-doctor-wrapper {
    width: 33%;
  }

  .header-dashboard-wrapper .header .menu-wrapper .login-wrapper {
    justify-content: space-around;
    width: 100%;
  }

  .dashboard-section .details-action-container .details-action-wrapper h2 {
    font-size: 20px;
    font-weight: 700;
    line-height: 31px;
  }
}

@media only screen and (max-width: 768px) {
  .button {
    padding: 11px 12px;
  }
  .trust-us-section .boxes-wrapper .boxes {
    margin-bottom: 29px;
  }
  .consulting-specialist-section .small-boxes-wrapper .boxes-sm-bx {
    width: calc((100% / 3));
  }
  .about-text-wrapper-all {
    width: 100%;
  }

  .asked-question-section .faq-img-wrapper {
    display: block;
  }
  .asked-question-section .faq-img-wrapper .cm-bx-faq {
    width: 100%;
  }
  .asked-question-section
    .faq-img-wrapper
    .img-faq-sec
    .round-div
    .img-wrapper {
    position: relative;
    right: 0;
    top: 0;
  }
  .asked-question-section .faq-img-wrapper .img-faq-sec .round-div {
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
  }
  .dashboard-section {
    flex-wrap: wrap;
    min-height: auto;
    padding-bottom: 4rem;
  }

  .dashboard-section .details-action-container {
    position: absolute;
    top: 65px;
    left: 0px;
    width: 80%;
    background: #fdf9f594;
    padding: 6px;
  }

  .dashboard-section img {
    position: static;
  }
  .header-dashboard-wrapper .header .menu-wrapper > * {
    margin: 0;
  }

  .dashboard-section .details-action-container .details-action-wrapper h2 {
    font-size: 13px;
    font-weight: 700;
    line-height: 18px;
  }
  .mybtn {
    padding: 6px 25px;
    border: none;
    border-radius: 24px;
    margin: 9px 10px 2px 0px;
    font-size: 12px;
  }
}

@media only screen and (max-width: 640px) {
  .start-online-chat-section .img-chat-wrapper .img-wrapper {
    max-width: 100%;
  }

  .start-online-chat-section .img-chat-wrapper .chat-wrapper {
    width: 100%;
    position: static;
    right: 0;
    top: 0;
    margin-top: 10px;
  }
  .start-online-chat-section .img-chat-wrapper .chat-wrapper .chat {
    align-items: center;
  }

  .start-online-chat-section
    .consult-doctor-details-wrapper
    .input-action-wrapper
    .checkbox-and-icon-wrapper {
    display: flex;
    width: 100%;
    margin: 6px;
  }

  .consulting-specialist-section .small-boxes-wrapper .boxes-sm-bx {
    width: calc((100% / 2));
  }
  .footer .about-webpage-wrapper,
  .footer .contact-wrapper,
  .footer .important-wrapper,
  .footer .get-wrapper,
  .footer .extra-stuff-wrapper {
    width: 100%;
  }
  .footer .about-webpage-wrapper .text-wrapper {
    width: 100%;
  }
  .footer .contact-wrapper {
    text-align: left;
    ul {
      display: block;
      text-align: left;
    }
  }
}

@media only screen and (max-width: 480px) {
  .dashboard-section .details-action-container {
    top: 16px;
  }
  .consulting-specialist-section .small-boxes-wrapper .boxes-sm-bx {
    width: 100%;
  }
  .dashboard-section .details-action-container .details-action-wrapper {
    font-size: 12px;
  }
  .button {
    padding: 4px 21px;

    margin: 5px 0px 2px 0px;
    font-size: 10px;
  }
  .dashboard-section .details-action-container .details-action-wrapper p {
    margin: 15px 0;
  }
  .header-dashboard-wrapper .header .menu-wrapper {
    display: block;
  }

  .header-dashboard-wrapper .header .site-logo-container img {
    width: 25%;
    max-height: 32px;
    margin-bottom: 12px;
  }
  .header-dashboard-wrapper .header .menu-wrapper .menu-show-hide-button {
    height: 27px;
    width: 26px;
  }
  .our-recent-articles-section .articles-wrapper .article-box .article-img {
    height: 200px;
  }
  .our-recent-articles-section
    .articles-wrapper
    .article-box
    .articles-records-wrapper {
    top: 10rem;
    left: 7px;
    height: 27px;
  }
  .our-recent-articles-section
    .articles-wrapper
    .article-box
    .doctor-details-wrapper
    .about-doctor-wrapper {
    width: 100%;
  }
  .asked-question-section
    .faq-img-wrapper
    .img-faq-sec
    .round-div
    .img-wrapper {
    height: 250px;
  }
  .footer .logo-wrapper img {
    max-height: 63px;
    margin: 12px 0;
  }
}
